class Downloader extends PureComponent {
  state = {
    imgLoading: false,
    pdfLoading: false,
  }

  componentDidMount() {
    html2canvas = require('html2canvas')
    require('whatwg-fetch')
    require('vendor/canvas-toBlob')
    this.takeWith = `//${window.location.host}${withPrefix('takeWith.pdf')}`
  }

  screenDom = createRef()
  a4Dom = createRef()

  setImgLoading = imgLoading => this.setState({ imgLoading })
  setPdfLoading = pdfLoading => this.setState({ pdfLoading })

  setLoading = key => {
    this.saveScroll()
    this.setState({ [`${key}Loading`]: true })
  }

  unsetLoading = key => {
    this.restoreScroll()
    this.setState({ [`${key}Loading`]: false })
  }

  saveScroll = () => {
    this.lastScrollY = window.scrollY
    window.scrollTo(0, 0)
  }

  restoreScroll = () => {
    window.scrollTo(0, this.lastScrollY)
  }

  render() {
    const { memo, isMobile, noticeList, inModal, mb, ...props } = this.props
    const { imgLoading, pdfLoading } = this.state;

    return (
      <Fragment>
        <Flex
          mt="1em"
          mx="1.25em"
          mb={inModal || mb ? 0 : '1em'}
          flexDirection={inModal && 'column'}
          flexWrap={mobileOrDesktop('wrap', 'nowrap')}
          justifyContent="center"
          {...props}
        >
          <Button.download
            my="0.25em"
            width={1}
            disabled={imgLoading}
            onClick={() => {
              this.setLoading('img')
              html2canvas(this.screenDom.current).then((canvas) => {
                canvas.toBlob((blob) => {
                  this.unsetLoading('img')
                  saveAs(blob, 'doctor-memo.png');
                })
              });
            }}
          >
            <Box.inline width="12em">{`下載為適合${isMobile ? '手機螢幕' : '直接分享'}的圖片`}</Box.inline>
          </Button.download>
          {isIos ? <Text mt="0.5em">(開啟圖片後，需手動另存新檔)</Text> : (
            <Button.download
              my="0.25em"
              width={1}
              disabled={pdfLoading}
              onClick={() => {
                this.setLoading('pdf')
                if (memo) {
                  Promise.all([
                    PDFDocument.create(),
                    window.fetch(this.takeWith)
                      .then(res => res.arrayBuffer())
                      .then((bytes) => PDFDocument.load(bytes)),
                    html2canvas(this.a4Dom.current),
                  ]).then(([
                    pdfDoc,
                    takePdf,
                    canvas,
                  ]) => {
                    const page = pdfDoc.addPage([a4Landscape.width, a4Landscape.height])
                    const ratio = canvas.height / canvas.width;
                    const height = a4Landscape.width * ratio
                    Promise.all([
                      pdfDoc.copyPages(takePdf, [0]),
                      pdfDoc.embedPng(canvas.toDataURL()),
                    ]).then(([
                      [existingPage],
                      img,
                    ]) => {
                      page.drawImage(img, {
                        x: 0,
                        y: (a4Landscape.height - height) / 2,
                        width: a4Landscape.width,
                        height,
                      })
                      if (noticeList) {
                        pdfDoc.insertPage(1, existingPage)
                      }
                      pdfDoc.save().then((arrayBuffer) => {
                        saveAs(new Blob([arrayBuffer]), 'doctor-memo.pdf');
                        this.unsetLoading('pdf')
                      })
                    })
                  })
                } else {
                  saveAs(this.takeWith, 'doctor-memo.pdf');
                  this.unsetLoading('pdf')
                }
              }}
            >
              <Box.inline width="12em">下載為可列印的PDF檔案</Box.inline>
            </Button.download>
          )}

        </Flex>
        {myPortal(
          <Box position="absolute" top="-9999px" left="-9999px" opacity="0" width="100%">
            <Box ref={this.screenDom} bg="lightBgOrange">
              <Container py="2em">
                {memo && (
                  <DateCard isDownload py="1em">
                    <MemoResult isDownload memo={memo} />
                  </DateCard>
                )}
                {noticeList || null}
              </Container>
            </Box>
          </Box>
        )}
        {myPortal(
          <Box position="absolute" top="-9999px" left="-9999px" opacity="0">
            <Box ref={this.a4Dom}>
              <A4Page px="2cm" py="1cm">
                {memo && (
                  <DateCard isDownload>
                    <MemoResult isDownload memo={memo} forceDesktop />
                  </DateCard>
                )}
              </A4Page>
            </Box>
          </Box>
        )}
        {myPortal((imgLoading || pdfLoading) && (
          <Box position="fixed" top="0" left="0" right="0" bottom="0" bg="rgba(0,0,0,0.8)" zIndex={9999}>
            <Box position="absolute" top="50%" left="50%" transform="translate(-50%, -50%)" color="white" fontSize="1.5em">
              檔案生成中...
            </Box>
          </Box>
        ))}
      </Fragment>
    );
  }
}

export default withResponsive(Downloader);
