import React from 'react';
import format from 'date-fns/format'

import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Image from 'components/Image'
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import cal from './icon-cal.png'

const DateCard = ({ date, children, isDownload, ...props }) => {
  return (
    <Box
      mx={mobileOrDesktop('1em', '0.5em')}
      mt="1em"
      pb={mobileOrDesktop('0.25em', '1em')}
      boxShadow={!isDownload && '-0px 3px 4px 4px rgba(0,0,0,0.1)'}
      bg="white"
      borderRadius="1em"
      {...props}
    >
      <Flex
        bg={!isDownload && 'noteBg'}
        p="1em"
        borderRadius="1em 1em 0 0"
        height="3em"
        color={isDownload ? 'noteBg' : 'white'}
        alignItems="center"
      >
        {isDownload ? <Image mr="1em" src={cal} width="1.5em" height="1.5em" /> : <Box transform="rotate(90deg)" mr="0.5em" ml={isDownload && '1em'}>▲</Box>}
        <Text fontSize="1.5em" fontWeight="bold">{format(date, 'YYYY/MM/DD')}</Text>
      </Flex>
      {children}
    </Box>
  );
};

DateCard.defaultProps = {
  date: new Date()
}

export default DateCard;
